body{
    font-family: parse-grotesk-mono-regular, sans-serif;
}
h1{
    font-family: "Courier-10-Pitch-Bold", sans-serif;
}
h2{
    font-family: "Courier-10-Pitch-Bold", sans-serif;
}
h4{
    font-family: "Courier-10-Pitch-Bold", sans-serif;
}
h5{
    font-family: "Courier-10-Pitch-Bold", sans-serif;
}
p{
    font-family: parse-grotesk-mono-regular, sans-serif;
}
.back{
    color: black;
    background-color: #b4dac1;
    padding: 3px;
    font-family: parse-grotesk-mono-bold, sans-serif;
    /*#faccaa*/
    /*#a1bde7*/
}
.proback{
    color: black;
    background-color: #b4dac1;
    padding: 3px;
    font-family: "Courier-10-Pitch-Bold", sans-serif;
}
.links{
    font-family: parse-grotesk-mono-regular, sans-serif;
    text-decoration: underline;
}
a{
    color: #212529 !important;
}
a:hover{
    color: #b4dac1 !important;
}
.web-link{
    text-decoration: none !important;
    font-weight: bold;
}
.love{
    color:#b4dac1 !important ;
    font-size: 15px;
}
.alert{
    background-color: #b4dac1;
    font-family: parse-grotesk-mono-regular, sans-serif;
}
.breadcrumb{
    background-color: white !important;
    float: right ;
    text-decoration: none;
    font-family: parse-grotesk-mono-regular, sans-serif;
}

.breadcrumb-item + .breadcrumb-item::before {
    color: #b4dac1;
    font-family: parse-grotesk-mono-regular, sans-serif;
}
.breadcrumb-item + .breadcrumb-item:hover::before {
    text-decoration: none !important;
}
.name{
    font-family: parse-grotesk-mono-bold, sans-serif;
}
.loop{
    font-family: parse-grotesk-mono-bold, sans-serif;
}
@media (max-width:598px)
{
    .photo{

        width: 150px;
        height: 150px;
        border-radius: 50%;
    }
    .media{
    display: inline;
    text-align: center !important;
    }
    .project{
        text-align: center;
    }
}
/*-----------------------------------Fonts-------------------------------*/
@font-face {
    font-family: "Courier";
    src: url(fonts/courier-prime-sans.ttf) format("truetype");
}
@font-face {
    font-family: "Courier-10-Pitch-Bold";
    src: url(fonts/Courier-10-Pitch-Bold.ttf) format("truetype");
}
@font-face {
    font-family: "parse-grotesk-mono-thin";
    src: url(fonts/parsegroteskmono_thin.otf) format("opentype");
}

@font-face {
    font-family: "parse-grotesk-mono-regular";
    src: url(fonts/parsegroteskmono_regular.otf) format("opentype");
}

@font-face {
    font-family: "parse-grotesk-mono-bold";
    src: url(fonts/parsegroteskmono_bold.otf) format("opentype");
}